import React from "react";


import DataFilters from "./components/DataFilters"
import Datasets from "./components/Datasets"
import Pagination from "./components/Pagination"


import { loadDatasets } from "./api"


class Data extends React.Component {


  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      searchTerm: "",
      count: 0,
      pages: 0,
      currentPage: 0,
      limit: 24,
      cards: [],
      selectedObjectTypes: [],
      selectedLicenses: [],
      selectedTheme: [],
      selectedEvents: [],
      selectedInstitution: "",
    }
  }

  handlePaginationClick = async (e, page) => {
    this.setState({
      ...this.state,
      currentPage: page
    }, async () => {
      await this.loadData()
    })
  }

  updateObjectTypes = async (data) => {
    this.setState({
      selectedObjectTypes: data
    }, async () => {
      await this.loadData()
    })
  }

  updateLicenses = async (data) => {
    this.setState({
      selectedLicenses: data
    }, async () => {
      await this.loadData()
    })
  }

  updateTheme = async (data) => {
    this.setState({
      selectedTheme: data
    }, async () => {
      await this.loadData()
    })
  }

  updateEvents = async (data) => {
    this.setState({
      selectedEvents: data
    }, async () => {
      await this.loadData()
    })
  }

  doSearch = async (q, ins) => {
    this.setState({
      searchTerm: q,
      selectedInstitution: ins,
    }, async () => {
      await this.loadData()
    })
  }

  loadData = async () => {
    const limit = this.state.limit
    const offset = this.state.currentPage * limit

    const objecttypes = this.state.selectedObjectTypes.join(",")
    const events = this.state.selectedEvents.join(",")
    const licenses = this.state.selectedLicenses.join(",")
    const theme = this.state.selectedTheme.join(",")
    const institution = this.state.selectedInstitution

    const cards = await loadDatasets(offset, events, objecttypes, licenses, theme, institution, this.state.searchTerm)

    this.setState({
      ...this.state,
      cards: cards.data,
      pages: Math.ceil(cards.count / limit),
      loading: false,
    }, async () => {

    })
  }

  async componentDidMount() {
    this.loadData()
  }

  render() {
    if (this.state.loading) {
      return "...."
    }

    return (
      <div className="view view-data-sets view-id-data-sets view-display-id-page_1 container">
        <div className="row">
          <DataFilters
            updateObjectTypes={this.updateObjectTypes}
            updateLicenses={this.updateLicenses}
            updateTheme={this.updateTheme}
            updateEvents={this.updateEvents}
            doSearch={this.doSearch}
          />
          <Datasets cards={this.state.cards} />
          <Pagination
            total={this.state.pages}
            current={this.state.currentPage}
            onChange={this.handlePaginationClick}
            limit={this.state.limit}
          />
        </div>
      </div>
    )
  }
}

export default Data