import React from "react";
import Autosuggest from 'react-autosuggest';

import { loadObjectTypes, loadEvents, loadLicenses, loadTheme, loadInstitutions } from "../api"

class DataFilters extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      searchTerm: "",
      selectedInstitution: "",
      institutionSuggestions: [],
      objectTypes: [],
      events: [],
      licenses: [],
      theme: [],
      selectedObjectTypes: [],
      selectedEvents: [],
      selectedTheme: [],
      selectedLicenses: [],
      institutions: [],
    }
  }

  renderSuggestion(suggestion) {
    return (
      <span>{suggestion.name}</span>
    );
  }

  getSuggestionValue(suggestion) {
    return suggestion.name;
  }

  selectObjectType = (tid) => {
    let selectedObjectTypes = this.state.selectedObjectTypes

    if (this.state.selectedObjectTypes.includes(tid)) {
      selectedObjectTypes = selectedObjectTypes.filter(item => item !== tid)
    } else {
      selectedObjectTypes.push(tid)
    }

    this.setState({ selectedObjectTypes }, async () => {
      this.props.updateObjectTypes(selectedObjectTypes)
    })
  }

  selectLicense = (tid) => {
    let selectedLicenses = this.state.selectedLicenses

    if (this.state.selectedLicenses.includes(tid)) {
      selectedLicenses = selectedLicenses.filter(item => item !== tid)
    } else {
      selectedLicenses.push(tid)
    }

    this.setState({ selectedLicenses }, async () => {
      this.props.updateLicenses(selectedLicenses)
    })
  }

  selectTheme = (tid) => {
    let selectedTheme = this.state.selectedTheme

    if (this.state.selectedTheme.includes(tid)) {
      selectedTheme = selectedTheme.filter(item => item !== tid)
    } else {
      selectedTheme.push(tid)
    }

    this.setState({ selectedTheme }, async () => {
      this.props.updateTheme(selectedTheme)
    })
  }

  selectEvent = (nid) => {
    let selectedEvents = this.state.selectedEvents

    if (this.state.selectedEvents.includes(nid)) {
      selectedEvents = selectedEvents.filter(item => item !== nid)
    } else {
      selectedEvents.push(nid)
    }

    this.setState({ selectedEvents }, async () => {
      this.props.updateEvents(selectedEvents)
    })
  }

  onChange = (event) => {
    this.setState({ searchTerm: event.target.value })
  }

  onChangeInstitution = (event, { newValue, method }) => {
    this.setState({
      selectedInstitution: newValue
    });
  };

  getSuggestions = value => {
    const inputValue = value.trim().toLowerCase();
    const inputLength = inputValue.length;

    let institutions = this.state.institutions

    return inputLength === 0 ? [] : institutions.filter(institution =>
      // Find from Begin to String:
      // institution.name.toLowerCase().slice(0, inputLength) === inputValue
      // Find string in string:
      institution.name.toLowerCase().indexOf(inputValue)>-1
    );
  };

  onSuggestionsFetchRequested = ({ value }) => {
    // Autosuggest will call this function every time you need to update suggestions.
    this.setState({
      institutionSuggestions: this.getSuggestions(value)
    });
  };

  onSuggestionsClearRequested = () => {
    // Autosuggest will call this function every time you need to clear suggestions.
    this.setState({
      institutionSuggestions: []
    });
  };

  doSearch = (e) => {
    e.preventDefault()

    if (this.state.searchTerm || this.state.selectedInstitution !== "") {
      this.props.doSearch(this.state.searchTerm, this.state.selectedInstitution)
    }
  }

  doReset = (e) => {
    e.preventDefault()
    this.setState({ selectedObjectTypes: [], selectedEvents: [], selectedTheme: [], selectedLicenses: [], selectedInstitution: "", searchTerm: "" }, async () => {
      this.props.updateObjectTypes([])
      this.props.updateEvents([])
      this.props.updateLicenses([])
      this.props.updateTheme([])
      this.props.doSearch("", "")
    })
  }

  async componentDidMount() {
    // try catch
    const objectTypes = await loadObjectTypes()
    const events = await loadEvents()
    const licenses = await loadLicenses()
    const theme = await loadTheme()
    const institutions = await loadInstitutions()

    this.setState({
      ...this.state,
      objectTypes: objectTypes,
      events: events,
      licenses: licenses,
      theme: theme,
      institutions: institutions,
      loading: false,
    }, async () => {

    })
  }

  renderObjectTypes = (term, key) => {
    if (this.state.selectedObjectTypes.includes(term.tid)) {
      return (<li key={key}><div className="badge"><a href="#" className="bef-link bef-link--selected badge" onClick={() => this.selectObjectType(term.tid)}>{term.title}</a></div></li>)
    } else {
      return (<li key={key}><div className="badge"><a href="#" className="bef-link badge" onClick={() => this.selectObjectType(term.tid)}>{term.title}</a></div></li>)
    }
  }

  renderLicenses = (term, key) => {
    if (this.state.selectedLicenses.includes(term.tid)) {
      return (<li key={key}><div className="badge"><a href="#" className="bef-link bef-link--selected badge" onClick={() => this.selectLicense(term.tid)}>{term.title}</a></div></li>)
    } else {
      return (<li key={key}><div className="badge"><a href="#" className="bef-link badge" onClick={() => this.selectLicense(term.tid)}>{term.title}</a></div></li>)
    }
  }

  renderEvent = (event, key) => {
    if (this.state.selectedEvents.includes(event.nid)) {
      return (<li key={key}><div className="badge"><a href="#" className="bef-link bef-link--selected badge" onClick={() => this.selectEvent(event.nid)}>{event.title}</a></div></li>)
    } else {
      return (<li key={key}><div className="badge"><a href="#" className="bef-link badge" onClick={() => this.selectEvent(event.nid)}>{event.title}</a></div></li>)
    }
  }

  renderTheme = (event, key) => {
    if (this.state.selectedTheme.includes(event.tid)) {
      return (<li key={key}><div className="badge"><a href="#" className="bef-link bef-link--selected badge" onClick={() => this.selectTheme(event.tid)}>{event.title}</a></div></li>)
    } else {
      return (<li key={key}><div className="badge"><a href="#" className="bef-link badge" onClick={() => this.selectTheme(event.tid)}>{event.title}</a></div></li>)
    }
  }

  render() {
    if (this.state.loading) {
      return "...."
    }

    return (
      <div className="view-filters col-lg-3">
        <form className="views-exposed-form bef-exposed-form" >
          <div className="form--inline clearfix row">
            <div className="col-12">
              <div className="js-form-item form-item js-form-type-select form-type-select js-form-item-project-type form-item-project-type">
                <label htmlFor="edit-project-type">Event</label>
                <div data-drupal-selector="edit-project-type" multiple="multiple" name="project_type" id="edit-project-type" size="9" className="form-select bef-links">
                  <ul>
                    {this.state.events.map((term, key) => {
                      return (this.renderEvent(term, key))
                    })}
                  </ul>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="js-form-item form-item js-form-type-select form-type-select js-form-item-event form-item-event">
                <label htmlFor="edit-event">Objekttyp</label>
                <div multiple="multiple" name="event" id="edit-event" size="8" className="form-select bef-links">
                  <ul>
                    {this.state.objectTypes.map((term, key) => {
                      return (this.renderObjectTypes(term, key))
                    })}
                  </ul>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="js-form-item form-item js-form-type-select form-type-select js-form-item-event form-item-event">
                <label htmlFor="edit-event">Lizenz Mediendateien</label>
                <div multiple="multiple" name="event" id="edit-event" size="8" className="form-select bef-links">
                  <ul>
                    {this.state.licenses.map((term, key) => {
                      return (this.renderLicenses(term, key))
                    })}
                  </ul>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="js-form-item form-item js-form-type-select form-type-select js-form-item-event form-item-event">
                <label htmlFor="edit-event">Thema</label>
                <div multiple="multiple" name="event" id="edit-event" size="8" className="form-select bef-links">
                  <ul>
                    {this.state.theme.map((term, key) => {
                      return (this.renderTheme(term, key))
                    })}
                  </ul>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="js-form-item form-item js-form-type-textfield form-type-textfield js-form-item-search form-item-search">
                <label htmlFor="edit-search">Institution</label>
                <Autosuggest
                  suggestions={this.state.institutionSuggestions}
                  onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
                  onSuggestionsClearRequested={this.onSuggestionsClearRequested}
                  getSuggestionValue={this.getSuggestionValue}
                  renderSuggestion={this.renderSuggestion}
                  inputProps={{placeholder: "", value: this.state.selectedInstitution, onChange: this.onChangeInstitution }} />
              </div>
            </div>
            <div className="col-12">
              <div className="js-form-item form-item js-form-type-textfield form-type-textfield js-form-item-search form-item-search">
                <label htmlFor="edit-search">Volltextsuche</label>
                <input type="text" id="edit-search" name="search" value={this.state.searchTerm} size="30" maxLength="128" className="form-text" onChange={this.onChange}/>
              </div>
            </div>
            <div className="col-12">
              <div data-drupal-selector="edit-actions" className="form-actions js-form-wrapper form-wrapper" id="edit-actions">
                <input type="submit" value="Filter anwenden" className="button js-form-submit form-submit" onClick={this.doSearch}/>
                <input type="submit" value="Zurücksetzen" className="button js-form-submit form-submit" onClick={this.doReset}/>
              </div>
          </div>
        </div>
      </form>
    </div>
    );
  }
}

export default DataFilters;