export async function loadCards(offset, projects = "", events = "", q = "") {
  const response = await axios.get(
    `${API_HOST}/dd_views/cards?offset=${offset}&projects=${projects}&events=${events}&q=${q}`,
    {
      headers: {
        'Content-Type': 'application/json',
      }
    }
  ).catch(error => {
    throw (error.response)
  })

  return response.data
}

export async function loadDatasets(offset, events = "", objecttypes = "", licenses = "", theme = "", institution="", q = "") {
  const response = await axios.get(
    `${API_HOST}/dd_views/datasets?offset=${offset}&objecttypes=${objecttypes}&events=${events}&licenses=${licenses}&theme=${theme}&institution=${institution}&q=${q}`,
    {
      headers: {
        'Content-Type': 'application/json',
      }
    }
  ).catch(error => {
    throw (error.response)
  })

  return response.data
}