
function Datasets(props) {

  if (!props.cards.length) {
    return <div className="col-lg-9">
            <div className="">
              Es sind keine den Filtereinstellungen entsprechenden Datensets vorhanden.
            </div>
          </div>
  }

  return (
    <div className="view-content col-lg-9">
      <div className="card-columns">
        {props.cards.map((card, key)=>{
            return (<div className="views-row card" key={key}>
              <article className="node node--type-data-set node--view-mode-teaser">
                <div className="card-img card-img-top">
                  <a href={card.url}>
                    <div className="field field--name-field-attributed-image field--type-entity-reference-revisions field--label-hidden field__item">
                      <div className="paragraph paragraph--type--attributed-image paragraph--view-mode--teaser">
                        <div className="field field--name-field-inline-image field--type-image field--label-hidden field__item">
                          <img src={card.image_url} width="480" height="256" alt="" className="image-style-large"/>
                        </div>
                        <div className="paragraph--type--attributed-image--attribution"></div>
                      </div>
                    </div>
                  </a>
                </div>
                <div className="card-body">
                  <h2 className="card-title">
                    <a href={card.url} rel="bookmark">
                      <span className="field field--name-title field--type-string field--label-hidden">{card.title}</span>
                    </a>
                  </h2>
                  <div className="node__content">
                    <div className="field field__items field--institution-name">
                      <div className="field__item">{card.author_name}</div>
                    </div>
                    <div className="field field--name-field-events field--type-entity-reference-revisions field--label-hidden field__items">
                      <div className="field__item">
                        <div className="paragraph paragraph--type--data-set-event paragraph--view-mode--preview">
                          <div className="field field--name-field-event field--type-entity-reference field--label-visually_hidden">
                            <div className="field__label visually-hidden">Veranstaltung</div>
                              {card.event_titles.map((event, index) => {
                                return <div className="field__item" key={index}>
                                  <a href={event.url}>{event.title}</a>
                                </div>
                              })}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="field field--name-field-object-type field--type-entity-reference field--label-hidden field__items">
                      {card.objecttypes_titles.map((project_type, index) => {
                        return (<div key={index} className="badge field__item"><span>{project_type}</span></div>)
                      })}
                    </div>
                  </div>
                </div>
              </article>
            </div>
        )})}
      </div>
    </div>
  )
}

export default Datasets;