import React from "react";



class Pagination extends React.Component {

  pagination(prevPage, nextPage) {
    const out = []

    const currentPage = this.props.current + 1
    const hasPreviousLink = Boolean(currentPage > 1 && prevPage !== this.props.total)
    const hasNextLink = Boolean(nextPage >= currentPage && nextPage !== this.props.total)

    const hasFirstLink = hasPreviousLink
    const hasLastLink = hasNextLink

    if (hasFirstLink) { // «
      out.push(<li className="pager__item pager__item--next">
        <a href="#" title="Zur ersten Seite" rel="next" onClick={(e) => this.props.onChange(e, 0)}>
          <span className="visually-hidden">Erste Seite</span>
          <span aria-hidden="true">&#171;</span>
        </a>
      </li>)
    }

    if (hasPreviousLink) {
      out.push(
        <li className="pager__item pager__item--previous">
        <a href="#" title="Zur vorherigen Seite" rel="prev" onClick={(e) => this.props.onChange(e, prevPage)}>
          <span className="visually-hidden">Vorherige Seite</span>
          <span aria-hidden="true">‹</span>
        </a>
      </li>
      )
    }

    for (let i = 1; i < (this.props.total + 1); i++) {

      const pushPage = ((i < 3) || (i > (this.props.total - 2)) || (i === currentPage)) ? true : false

      if (pushPage) {
        const classNameLi = (i === currentPage) ? "pager__item is-active" : "pager__item"
        out.push(<li className={classNameLi} key={i}>
          <a href="#" onClick={(e) => this.props.onChange(e, (i - 1))}>
            {i}
          </a>
        </li>)
      }
      else if (
        (this.props.total > 4)
        && (i !== currentPage)
        && (i === 3)
      ) {
        const display = (this.props.total === 5) ? '3' : '...'
        out.push(<li className="pager__item disabled" key={i}>
          <a href="#" >
            {display}
          </a>
        </li>)
      }
      else if (
        (this.props.total > 4)
        && (i !== currentPage)
        && (i === (this.props.total - 2))
        && !(
          (currentPage < 3)
          || (currentPage > (this.props.total - 2))
        )
      ) {
        out.push(<li className="pager__item disabled" key={i}>
          <a href="#">
            ...
          </a>
        </li>)
      }
    }

    if (hasNextLink) {
      out.push(<li className="pager__item pager__item--next">
        <a href="#" title="Zur nächsten Seite" rel="next" onClick={(e) => this.props.onChange(e, nextPage)}>
          <span className="visually-hidden">Nächste Seite</span>
          <span aria-hidden="true">›</span>
        </a>
      </li>)
    }

    if (hasLastLink) { // »
      out.push(<li className="pager__item pager__item--next">
        <a href="#" title="Zur letzten Seite" rel="next" onClick={(e) => this.props.onChange(e, this.props.total-1)}>
          <span className="visually-hidden">Letzte Seite</span>
          <span aria-hidden="true">&#187;</span>
        </a>
      </li>)
    }

    return out;
  }

  render() {

    const prevPage = ((this.props.current - 1) >= 0) ? this.props.current - 1 : 0
    const nextPage = ((this.props.current + 1) <= this.props.total) ? this.props.current + 1 : this.props.total

    return (
      <div className="col-lg-9 offset-lg-3">
        <nav className="pager" role="navigation" aria-labelledby="pagination-heading">
          <h4 id="pagination-heading" className="visually-hidden">Seitennummerierung</h4>
            <ul className="pager__items js-pager__items">
              {this.pagination(prevPage, nextPage)}
            </ul>
          </nav>
        </div>
    );
  }
}
export default Pagination;